import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const Header: React.FC = () => {
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const scrollToCatalog = () => {
    const catalogSection = document.getElementById('catalog');
    if (catalogSection) {
      catalogSection.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  const scrollToContact = () => {
    const contactSection = document.getElementById('contact');
    if (contactSection) {
      contactSection.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  const scrollToTop = () => {
    window.scrollTo({ 
      top: 0, 
      behavior: 'smooth' 
    });
  };

  return (
    <motion.header
      initial={{ y: -100 }}
      animate={{ y: 0 }}
      transition={{ duration: 0.6 }}
      className={`fixed top-0 left-0 right-0 z-[9999] transition-all duration-300 ${
        isScrolled 
          ? 'bg-white/95 backdrop-blur-md shadow-lg' 
          : 'bg-white/90 backdrop-blur-sm'
      }`}
    >
      <div className="container mx-auto px-4 py-3 md:py-4">
        <div className="flex items-center justify-between">
          {/*  */}
          <motion.div
            whileHover={{ scale: 1.05 }}
            onClick={scrollToTop}
            className="flex items-center space-x-2 cursor-pointer"
          >
            <div className="w-8 h-8 md:w-10 md:h-10 bg-primary-500 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-lg md:text-xl"></span>
            </div>
            <span className="text-lg md:text-xl font-bold text-gray-800">
              <span className="text-primary-500"></span>
            </span>
          </motion.div>

          {/*  */}
          <div className="hidden md:flex items-center space-x-6">
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={scrollToCatalog}
              className="btn-secondary"
            >
              
            </motion.button>
            
            <div className="text-right">
              <p className="text-sm text-gray-600"></p>
              <a 
                href="tel:+79111257070" 
                className="text-lg font-semibold text-gray-800 hover:text-primary-500 transition-colors"
              >
                +7 (911) 125-70-70
              </a>
            </div>
            
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={scrollToContact}
              className="btn-primary"
            >
               
            </motion.button>
          </div>

          {/*   */}
          <div className="md:hidden flex items-center space-x-2">
            <motion.button
              whileTap={{ scale: 0.95 }}
              onClick={scrollToCatalog}
              className="btn-secondary text-sm py-2 px-3"
            >
              
            </motion.button>
            <motion.button
              whileTap={{ scale: 0.95 }}
              className="btn-primary text-sm py-2 px-4"
            >
              
            </motion.button>
          </div>
        </div>
      </div>
    </motion.header>
  );
};

export default Header; 