import React, { useState } from "react";
import { motion } from "framer-motion";
import { useForm, ValidationError } from "@formspree/react";

interface FormData {
  name: string;
  phone: string;
  email: string;
  tileModel: string;
  area: string;
  message: string;
}

const tileModels = [
  "12 ",
  "",
  " ",
  "8 ",
  "",
  "",
  "",
  "",
  "3 ",
  " ",
  " ",
  "",
  " ",
  " ",
  " ",
  "",
  "",
  "  ",
  "",
  " "
];

const ContactForm: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    name: "",
    phone: "",
    email: "",
    tileModel: "",
    area: "",
    message: ""
  });

  //     ID  Formspree ( "xeozydwy")
  const [state, handleSubmit] = useForm("xeozydwy");

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value
    }));
  };

  if (state.succeeded) {
    return (
      <section className="py-20 bg-gray-900 text-white text-center">
        <h2 className="text-3xl font-bold">! </h2>
        <p className="text-lg mt-4">      .</p>
      </section>
    );
  }

  return (
    <section id="contact" className="py-20 bg-gray-900 pt-24 md:pt-28 text-white">
      <div className="container mx-auto px-4">
        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/*     */}
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <h2 className="text-4xl lg:text-5xl font-bold mb-6">
               <span className="text-gradient"></span> 
            </h2>
            <p className="text-xl text-gray-300 mb-8 leading-relaxed">
               ,         15 . 
                     .
            </p>
          </motion.div>

          {/*     */}
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
            className="bg-white/10 backdrop-blur-md rounded-3xl p-8 border border-white/20"
          >
            <form onSubmit={handleSubmit} className="space-y-6">
              {/*  */}
              <div>
                <label htmlFor="name" className="block text-sm font-medium text-gray-300 mb-2">
                    *
                </label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  required
                  className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:border-primary-500 transition-colors"
                  placeholder="  "
                />
                <ValidationError prefix="Name" field="name" errors={state.errors} />
              </div>

              {/*  */}
              <div>
                <label htmlFor="phone" className="block text-sm font-medium text-gray-300 mb-2">
                   *
                </label>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  value={formData.phone}
                  onChange={handleInputChange}
                  required
                  className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:border-primary-500 transition-colors"
                  placeholder="+7 (___) ___-__-__"
                />
                <ValidationError prefix="Phone" field="phone" errors={state.errors} />
              </div>

              {/* Email */}
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-300 mb-2">
                  Email
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:border-primary-500 transition-colors"
                  placeholder="your@email.com"
                />
                <ValidationError prefix="Email" field="email" errors={state.errors} />
              </div>

              {/*   */}
              <div>
                <label htmlFor="tileModel" className="block text-sm font-medium text-gray-300 mb-2">
                    
                </label>
                <select
                  id="tileModel"
                  name="tileModel"
                  value={formData.tileModel}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white focus:outline-none focus:border-primary-500 transition-colors"
                >
                  <option value=""> </option>
                  {tileModels.map((model) => (
                    <option key={model} value={model} className="text-gray-900">
                      {model}
                    </option>
                  ))}
                </select>
                <ValidationError prefix="TileModel" field="tileModel" errors={state.errors} />
              </div>

              {/*  */}
              <div>
                <label htmlFor="area" className="block text-sm font-medium text-gray-300 mb-2">
                    ()
                </label>
                <input
                  type="text"
                  id="area"
                  name="area"
                  value={formData.area}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:border-primary-500 transition-colors"
                  placeholder=": 100"
                />
                <ValidationError prefix="Area" field="area" errors={state.errors} />
              </div>

              {/*  */}
              <div>
                <label htmlFor="message" className="block text-sm font-medium text-gray-300 mb-2">
                   
                </label>
                <textarea
                  id="message"
                  name="message"
                  value={formData.message}
                  onChange={handleInputChange}
                  rows={4}
                  className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:border-primary-500 transition-colors resize-none"
                  placeholder="   ..."
                />
                <ValidationError prefix="Message" field="message" errors={state.errors} />
              </div>

              {/*  */}
              <motion.button
                type="submit"
                disabled={state.submitting}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="w-full bg-gradient-to-r from-primary-500 to-primary-600 text-white font-semibold py-4 px-8 rounded-lg hover:from-primary-600 hover:to-primary-700 transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {state.submitting ? "..." : " "}
              </motion.button>

              <p className="text-xs text-gray-400 text-center">
                 ,   {' '}
                <a 
                  href="/_____.pdf" 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="text-primary-400 hover:text-primary-300 underline transition-colors"
                >
                    
                </a>
              </p>
            </form>
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default ContactForm;
